/**
 * NetUitl 网络请求的实现
 */
import React, { Component } from 'react';
import api  from '../api/api';


class NetUitl extends React.Component {

    //post请求
    /**
     *url :请求地址
     *data:参数
     *callback:回调函数
     * 将app附加参数增加在链接后面
     */
    static  postFrom(url, data, callback) {
        try{

            var fetchOptions = {
                method: 'POST',
                headers: {},
                body:data //这里我参数只有一个data,大家可以还有更多的参数
            };
            url = this.getSendUrl(url);
            fetch(url, fetchOptions)
                .then((response) => response.text())
                .then((responseText) => {
                    callback(JSON.parse(responseText));
                }).done();
        }catch (e){
            alert('请求超时')
        }

    }

    static postJsonForHeaderAndAuth (url, data,token, callback) {
        var fetchOptions = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                //json形式
                'Content-Type': 'application/json',
                'Authorization':'Bearer '+token
            },
            body: JSON.stringify(data)
        };
        url = this.getSendUrl(url);
        let httpCode;
        fetch(url, fetchOptions)
            .then((response) => {
                httpCode = response.status;

                return response.text();
            })
            .then((responseText) => {
                
                let json = {'httpCode':httpCode,'json':''};
                if(responseText != ''){
                    json.json = JSON.parse(responseText);
                }
                callback(json);
            }).done();
    }

    static getJsonForHeaderAndAuth (url,token, callback) {
        var fetchOptions = {
            method: 'GET',
            headers: {
                'Accept': 'application/json',
                //json形式
                'Content-Type': 'application/json',
                'Authorization':'Bearer '+token
            },
        };
        url = this.getSendUrl(url);
        fetch(url,fetchOptions)
            .then((response) => response.text())
            .then((responseText) => {

                callback(JSON.parse(responseText));
            }).catch((error) =>{


        }).done();

    }

    static postJsonForHeader (url, data, callback) {
        var fetchOptions = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                //json形式
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        };
        // url = this.getSendUrl(url);
        let httpCode;
        fetch(url, fetchOptions)
            .then((response) =>{
                httpCode = response.status;
                return response.text();
            }).then((responseText) => {
                let json = {'httpCode':httpCode,'json':''};
                if(responseText != ''){
                    json.json = JSON.parse(responseText);
                }
                callback(json);
            }).done();

    }
    /**
     *url :请求地址
     *data:参数(Json对象)
     *callback:回调函数
     * 将app附加参数增加在链接后面
     */
    static postJson (url, data, callback) {
        var fetchOptions = {
            method: 'POST',
            headers: {
                'Accept': 'application/json',
                //json形式
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(data)
        };
        url = this.getSendUrl(url);
        fetch(url, fetchOptions)
            .then((response) => response.text())
            .then((responseText) => {
                callback(JSON.parse(responseText));
            }).done();
    }
    //get请求
    /**
     *url :请求地址
     *callback:回调函数
     * 将app附加参数增加在链接后面
     */
    static  get(url, callback) {
        try{
            url = this.getSendUrl(url);

            fetch(url)
                .then((response) => response.text())
                .then((responseText) => {

                    callback(JSON.parse(responseText));
                }).catch((error) =>{

            }).done();
        }catch (e){
        }

    }

    static getSendUrl(url){
        /*const channel = api.channel;
        const version = api.version;
        const idfa = DeviceInfo.getUniqueID();
        if(url.indexOf("?") >0){
            url = url +"&channel="+channel+"&version="+version+"&idfa="+idfa;
        }else{
            url = url +"?channel="+channel+"&version="+version+"&idfa="+idfa;
        }
        if(loginToken&& loginToken != '' && loginToken.length >0){
            url = url +"&token="+loginToken;
        }*/

        return url;

    }

}

module.exports = NetUitl;



