  _getTree(data,type){
    let id = data.id;
    let name = id.substring(id.length-5)+ (type ? '('+type+')':'');
    let leftPoint = data.leftPoint;
    let rightPoint = data.rightPoint;
    let rightChild = data.rightChild;
    let leftChild = data.leftChild;
    let leftInviteCode = data.leftInviteCode;
    let rightInviteCode = data.rightInviteCode;
    let pid = data.pid;
    let treeDataObj = {vid:'',name:'',leftPoint:'',rightPoint:'',rightChild:'',leftChild:'',pid:'',leftInviteCode:'',rightInviteCode:'',children:[]};
    let children = [];
    treeDataObj.name = name;
    treeDataObj.leftPoint = leftPoint;
    treeDataObj.rightPoint = rightPoint;
    treeDataObj.leftChild = leftChild;
    treeDataObj.rightChild = rightChild;
    treeDataObj.leftInviteCode = leftInviteCode;
    treeDataObj.rightInviteCode = rightInviteCode;
    treeDataObj.vid = data.id;
    treeDataObj.pid = pid;

    if(data.leftChildTree){
      let lData = this._getTree(data.leftChildTree,'左');
      children.push(lData);
    }
    // else{
    //   let lData = {name:'空节点'};
    //   children.push(lData)
    // }
    if(data.rightChildTree){
      let rData = this._getTree(data.rightChildTree,'右');
      children.push(rData);
    }
    // else{
    //   let rData = {name:'空节点'};
    //   children.push(rData);
    // }
    treeDataObj.children = children;
    return treeDataObj;
  }


