//转账
TransferYAO(slot,codeObj,allAddressArr){

    let token = this.state.token;
    if(token =='' || token.length <10){
        alert('请输入发币token')
        return;
    }
    let famount = this.state.amount;
    if(famount == '' || isNaN(famount)){
        alert('请输入账户金额');
        return;
    }

    const url = api.charge.transfer;
    const address = slot.wallet.address;
    const amount = parseInt(famount);
    const currency = 'YAO';
    const param = {"currency": currency, "address": address,"amount":amount};
    try {
        NetUtil.postJsonForHeaderAndAuth(url, param, token, (rtData) => {
            const {httpCode,json} = rtData;
            // const token = slot.token;
            // const mm = slot.wallet.mnemonic;
            if(httpCode == 200){
                this._createAccount(codeObj,allAddressArr);

            }else{
                alert('转账失败'+JSON.stringify(json))
                // console.info('error:'+JSON.stringify(json))
            }
        })
    } catch (error) {
        alert('转账失败')
        // console.info('error:'+error);
    }

}

//查询算力
_searchBalance = async () => {
  const walletInfo = this.walletList[this.walletSelectedIndex];
  const token = walletInfo.token;

  const url = api.searchBalance;
  try {
      NetUtil.getJsonForHeaderAndAuth(url, token, (rtData) => {
          const {spotAccounts} = rtData;
          if (spotAccounts && spotAccounts.length > 0) {

              let vid_share = new BigNumber(0),
                  vid_sharding = new BigNumber(0),
                  vid_lock_time = new BigNumber(0),
                  vid_hold = new BigNumber(0),
                  vid_check = new BigNumber(0),
                  vid_balance = new BigNumber(0),
                  total = new BigNumber(0);
              for (let i = 0; i < spotAccounts.length; i++) {
                  const {currency, balance, type} = spotAccounts[i];
                  if (currency == 'CF') {
                      if (type == 'VID_SHARE') {
                          vid_share = new BigNumber(balance);
                          total = total.plus(vid_share);
                      } else if (type == 'VID_SHARDING') {
                          vid_sharding = new BigNumber(balance);
                          total = total.plus(vid_sharding);
                      } else if (type == 'VID_LOCK_TIME') {
                          vid_lock_time = new BigNumber(balance);
                          total = total.plus(vid_lock_time);
                      } else if (type == 'VID_HOLD') {
                          vid_hold = new BigNumber(balance);
                          total = total.plus(vid_hold);
                      } else if (type == 'VID_CHECK') {
                          vid_check = new BigNumber(balance);
                          total = total.plus(vid_check);
                      } else if (type == 'VID_BALANCE') {
                          vid_balance = new BigNumber(balance);
                          total = total.plus(vid_balance);
                      }
                  }
              }
              this.setState({
                  vid_share:vid_share.toFixed(2),
                  vid_sharding:vid_sharding.toFixed(2),
                  vid_lock_time:vid_lock_time.toFixed(2),
                  vid_hold:vid_hold.toFixed(2),
                  vid_check:vid_check.toFixed(2),
                  vid_balance:vid_balance.toFixed(2),
                  total:total.toFixed(2),
                  myLoadStatus:true
              })
          }
      })
  } catch (error) {
      this.toastRef.current.show('请求超时');
  }

}

_loadMutilRules(callback){
  const url1 = api.mutilRules+"?type=LOCK_TIME,CALCULATE_FORCE";
  try {
      NetUtil.get(url1, (rtData) => {
          const {httpCode} = rtData;
          
          const {LOCK_TIME} = rtData;
          console.info(LOCK_TIME);
          
          if(LOCK_TIME && LOCK_TIME.length >0){
            let obj = {};
            
            for(let i=0; i<LOCK_TIME.length;i++){
              let item = LOCK_TIME[i];
              obj[item.id] = item.balance;
            }
            this.setState({
              lockTimeObj:obj
            })
              //YaoSettingStore.setLOCK_TIME(LOCK_TIME);
          }
          
      })

  } catch (error) {
  }
}

//查询节点总算力
handleClick(nodeData,evt){
  debugger;
  let vid = nodeData.vid;
  const token = this.state.token;
  const url = api.searchTotalPower+'?vid='+vid;
  try {
      NetUtil.getJsonForHeaderAndAuth(url, token, (rtData) => {
          const {spotAccounts} = rtData;
          if (spotAccounts && spotAccounts.length > 0) {

              let vid_share = new BigNumber(0),
                  vid_sharding = new BigNumber(0),
                  vid_lock_time = new BigNumber(0),
                  vid_hold = new BigNumber(0),
                  vid_check = new BigNumber(0),
                  vid_balance = new BigNumber(0),
                  total = new BigNumber(0);

              let lockBalance = new BigNumber(0);
              let day = 0;
              let avaiBalance = 0;
              for (let i = 0; i < spotAccounts.length; i++) {
                  const {currency, balance, type,lockTime,lockTimeRuleId} = spotAccounts[i];
                  if (currency == 'CF') {
                      if (type == 'VID_SHARE') {
                          vid_share = new BigNumber(balance);
                          total = total.plus(vid_share);
                      } else if (type == 'VID_SHARDING') {
                          vid_sharding = new BigNumber(balance);
                          total = total.plus(vid_sharding);
                      } else if (type == 'VID_LOCK_TIME') {//时间算力
                          vid_lock_time = new BigNumber(balance);
                          total = total.plus(vid_lock_time);
                      } else if (type == 'VID_HOLD') {
                          vid_hold = new BigNumber(balance);
                          total = total.plus(vid_hold);
                      } else if (type == 'VID_CHECK') {
                          vid_check = new BigNumber(balance);
                          total = total.plus(vid_check);
                      } else if (type == 'VID_BALANCE') {
                          vid_balance = new BigNumber(balance);
                          total = total.plus(vid_balance);
                      }
                  }else if(type =='SPOT_LOCKED'){
                      lockBalance = new BigNumber(balance);
                      if(lockBalance.comparedTo(new Number(0)) > 0){
                         const lockTimeObj = this.state.lockTimeObj; 
                          let item = lockTimeObj[lockTimeRuleId];
                          let balance = item.balance;
                          debugger;
                          day = parseFloat(item)/(1000*60*60*24);

                      }
                  }else if(type =='SPOT_AVAILABLE' && currency =='YAO'){

                      avaiBalance = new BigNumber(balance);
                  }
              }
              if(lockBalance.comparedTo(new Number("0"))==0){
                  lockBalance = avaiBalance;
              }
              this.setState({
                  nodeInfo:nodeData,
                  vid_share:vid_share.toFixed(2),
                  vid_sharding:vid_sharding.toFixed(2),
                  vid_lock_time:vid_lock_time.toFixed(2),
                  vid_hold:vid_hold.toFixed(2),
                  vid_check:vid_check.toFixed(2),
                  vid_balance:vid_balance.toFixed(2),
                  curNodePower:total.toFixed(2),
                  pointLoadStatus:true,
                  lockBalance:lockBalance.toFixed(2),
                  lockDay:day

              })
          }else{
            this.setState({nodeInfo:nodeData})
          }
      })
  } catch (error) {
  }

}


